.navbar {
    display: flex;
    height: 70px;
    padding: 0 30px;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 14.0351px 0px 25px rgba(86, 128, 248, 0.03), 35.0877px 0px 70px rgba(86, 128, 248, 0.05), 23.8596px 5.61404px 50px rgba(0, 0, 0, 0.02);
}
.navbar__element{
    display: flex;
}
.navbar h2{
    color: #0D1C2E;
    
    font-size: 36px;
}
.navbar__exit{
    font-size: 20px;
    margin-left: 30px;
    color: #707C97;
    border: none;
    background: none;
    cursor: pointer;
    transition: 1s;
}
.navbar__user{
    display: flex;
    align-items: center;
}
.navbar__user img{
    border-radius: 50%;
    margin-right: 10px;
    height: 60px;
    width: 60px;
}
.navbar__exit:hover{
    transform: scale(1.2)
}